function Contact(props){

    return(props.ctrigger)?(

        <div className="contact contactpop">
            <button className='btn btn-danger popclosebutton' onClick={()=>props.setctrigger(false)}>close x</button>
            
            <h1>Contact</h1>
            <p>
               Thanks for showing interest in this app,enter your details below ,we will guide you through adding the recipies.
            </p>
            <div className="container contactcontainer">
                        <form 
                                method='POST' 
                                name='contactform' 
                                className='contactForm'>

                                <input 
                                    className="form-control"
                                    type='hidden'
                                    name='form-name'
                                    value='contactForm' />
                                <label>YourName</label>
                                <input 
                                    className="form-control"
                                    required
                                    type='text' 
                                    name='name' 
                                    placeholder='Enter your name' />
                                <label>YourEmail</label>
                                <input 
                                    className="form-control"
                                    
                                    type='email' 
                                    name='email' 
                                    placeholder='Enter your email'
                                    required />
                                    <br/>
                                <label>Message</label>
                                <textarea 
                                    className="form-control"
                                     required
                                    name='message' 
                                    placeholder='tell us about your recipe / dish '></textarea>
                                    <br/>

                                <button className='btn btn-primary' type='submit'>Submit</button>
                       </form>
            </div>
        </div>
    ):<div></div>;
}
export default Contact;