function About(props){
    return(props.atrigger)?(
        <div className="about aboutpop">
            <button className='btn btn-danger popclosebutton' onClick={()=>props.setatrigger(false)}>Back</button>
            <div className="container aboutcontainer">
            <h1>About</h1>
            <p> Website under Development ,you can still share your recipes via mail,or contact us via "add my recipe" section ,if you find bugs or any improvements on the UI/UX suggestions are always welcomed :)  </p>
            </div>
        </div>



    ):<div></div>;

}
export default About;


    
                                
                        
   