
import {Navbar, Nav} from 'react-bootstrap';

import { useState,useEffect } from 'react';

import {Container} from 'react-bootstrap';

import {Form, Button} from 'react-bootstrap';

import About from './About';

import Contact from './Contact';

import Searchitems from './Searchitems';
import axios from 'axios';

const colorpref = '#f5f5f5';
const fork =require('./fork.png');

const searchurl = 'https://apisection.de.r.appspot.com';
// const searchurl ='http://localhost:3001';

function Navb(){

    const [about, setabout] = useState(false);

    const [contact, setcontact] = useState(false);

    const [search, setsearch] = useState(false);

    const[datapresent, setDatapresent] = useState(false);

    const [searchdata, setsearchdata] = useState([]);

    const [searchname, setsearchname] = useState('');

   function getsearchdata(name){

        //  axios.get(searchurl+'/search'+'/'+ name).then(res => {setsearchdata(res.data)}).catch(err => {console.log(err)})
        axios.get(searchurl+'/search'+'/'+ name).then(res => {setsearchdata(res.data)}).catch(err => {console.log(err)})

          //set timeoutfor search data to show
            setTimeout(()=>{

                setsearch(true);

            },1000);
    
    }       
    useEffect(()=>{
        if(searchdata.length>0){
            setDatapresent(true);
        }
        else{
            setDatapresent(false);
        }
    } ,[searchdata]);

 

    return(
        <Navbar id='nav' bg={colorpref} expand="lg" fixed="top" >

           <Container>

              <Navbar.Brand  id='navbrand' className='fs-3' href="">What'sSpecial?<img id='brandicon' src={fork}/></Navbar.Brand>

                  <Navbar.Toggle aria-controls="basic-navbar-nav" />

                      <Navbar.Collapse id="basic-navbar-nav">

                            <Nav className="me-auto">

                                <Nav.Link onClick={()=>setcontact(true)}>Add my recipe</Nav.Link>

                                <Nav.Link onClick={()=>setabout(true)}>About</Nav.Link>

                            </Nav>

                            <About atrigger={about} setatrigger={setabout} />

                            <Contact ctrigger={contact} setctrigger={setcontact} />

                            <Form className="d-flex">
                                <Form.Control

                                    type="search"

                                    placeholder="Search recipes"

                                    className="me-2"

                                    aria-label="Search"

                                    required="required"

                                    onChange={(e)=>setsearchname(e.target.value)}

                                    />
                                <Button variant="outline-success" onClick={()=>getsearchdata(searchname)}>Search</Button>

                                <Searchitems strigger={search} setstrigger={setsearch} searchdata={searchdata} datapresent={datapresent}/>

                            </Form>
                            
                      </Navbar.Collapse>

            </Container>

        </Navbar>

      
    );
}

export default Navb;