import { useState } from 'react';

import views from './views.png';

import axios from 'axios'; 

import Pop from './Pop';

const nonveg = require('./non-veg.png');

const veg= require('./veg.png');




const url = 'https://recipedot.herokuapp.com';

function NumtoDate(num){
    const date=new Date(num);
    return(date.toDateString());
    }

    const von=(category)=>{
      if(category==='V'){
          return veg;
      }
      else if(category==='N'){
          return nonveg;
      }
      else{
          return null;
      }
    }


function About(props){
    const [popdata, setPopdata] = useState([]);

    const[Popup, setPop] = useState(false);

    const getData=(image,name,description,ingredients,instructions,date,id,likes)=>{

        let tempdata={
    
          image:image,
    
          name:name,
    
          description:description,
    
          ingredients:ingredients,
    
          steps:instructions,
    
          date:date
    
        }
        setPopdata(tempdata);
        axios.put(url+'/likes/'+id).then(res=>{
    
          //refresh only the likes component
          }).catch(err=>{
   
            console.log(err);
          }
        )
    
        setPop(true)
      }


    return(props.strigger)?(
        <div className="about searchcont">
            <button className='btn btn-danger popclosebutton' onClick={()=>props.setstrigger(false)}>Back</button>
            <h1 id='poptext'>search Results</h1>
            
            <div className="container searchresults">

            {props.searchdata.map((item, index) => {
                        return(props.datapresent)?(
                          <>
                      <div className="card recipecard" key={index} >

                             <img  onClick={()=>getData(item.image,item.name,item.description,item.ingredients,item.steps,item.createdAt,item._id,item.likes)}  className="card-img" src={item.image[0]} />

                                    <div className="card-body">

                                       <h5 className="card-title fs-2">{item.name}</h5>

                                       <p className='fs-6'>recipe by {item.author} </p>
                                        <div className='views'><img className='viewsimage' src={views}/>{item.likes}</div>
                                        {/* <button  onClick={()=>getlikes(item._id,item.likes)} className="btn heartbutton"><img id='hearticon' src={like}/>{item.likes}</button> */}
                                        <img className='vegOrnon' src={von(item.category)} />

                                    </div>

                    
                      <div className="card-footer">

                        <p className='posted'>posted on {NumtoDate(item.createdAt)}</p>

                      </div>
                         
                      </div>
                      
                      </>
                        )
                        :<div className='container searchresults'>
                        <h1 className='display-1'>No Results Found</h1>
                        </div>
                    } )}

              

                </div>
                <Pop trigger={Popup} setTrigger={setPop} popdata={popdata}>
                
                </Pop>
        </div>

    ):<div></div>;

}
export default About;