
function Pop(props){

    return(props.trigger)?(

        <div className="popwindow container">

             <button className='btn btn-danger popclosebutton' onClick={()=>props.setTrigger(false)}>Back</button>

             <div className="pop-content">

                    <h1 id='poptext'>{props.popdata.name}</h1>

                    <img className='popwindowimage' src={props.popdata.image[0]} alt="food" />
                    <img className='popwindowimage' src={props.popdata.image[1]} alt="food" />
                    <img className='popwindowimage' src={props.popdata.image[2]} alt="food" />
                      <h1>Description</h1>
                      <p>
                            {props.popdata.description}
                        </p>

                      <h1>Ingredients</h1>
                   <ul>
                        {props.popdata.ingredients.map((item, index) => {
                            return (
                                <li key={index}>{item}</li>
                            )
                        } )}
                        
                   </ul>
                   <h1>Steps</h1>
                     <ul>
                        {props.popdata.steps.map((item, index) => {
                            return (
                                <li key={index}>{item}</li>
                            )
                        } )}
                    </ul>

                </div>
          </div>
        
    ):<div></div>;
    
}

export default Pop;