import axios from 'axios';

import { useState } from 'react';

import { useEffect } from 'react';

import views from './views.png';

import Pop from './Pop';

import share from './share.png';

import {
  MDBCarousel,
  MDBCarouselInner,
  MDBCarouselItem,
  MDBCarouselElement,
  MDBCarouselCaption,
} from 'mdb-react-ui-kit';

// const url ='http://localhost:3001/sort/likes';

const url = 'https://apisection.de.r.appspot.com/sort/likes';
const url2 = 'https://apisection.de.r.appspot.com';

function NumtoDate(num){
  const date=new Date(num);
  return(date.toDateString());
  }


const like = require('./like.png');

const nonveg = require('./non-veg.png');

const veg= require('./veg.png');

const von=(category)=>{
    if(category==='V'){
        return veg;
    }
    else if(category==='N'){
        return nonveg;
    }
    else{
        return null;
    }
  }

function Content(){
      
    const [data, setData] = useState([]);

    const [popdata, setPopdata] = useState([]);


    const[Popup, setPop] = useState(false);

    const getData=(image,name,description,ingredients,instructions,date,id,likes)=>{

      let tempdata={

        image:image,

        name:name,

        description:description,

        ingredients:ingredients,

        steps:instructions,

        date:date

      }

      setPopdata(tempdata);
      setPop(true);

      axios.put(url2+'/likes/'+id).then(res=>{
  
        //refresh only the likes component
        }).catch(err=>{
 
          console.log(err);
        }
      )
    }

  
   useEffect(() => {

       axios.get(url).then(res => {setData(res.data)}).catch(err => {console.log(err)})

    } , []);
       

 
   
    return(

      <>
            <div className="container homeRecipesContainer">

              <div className='container homepagecontainer'>

                    <MDBCarousel className='carousel' showControls interval={2000} fade>

                      <MDBCarouselInner>

                        <MDBCarouselItem className='active'>

                          <MDBCarouselElement  className="cimages" src='https://wallpapercave.com/wp/wp7845939.jpg' alt='...' />

                          <MDBCarouselCaption>

            <h5>First slide label</h5>

            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>

          </MDBCarouselCaption>

                        </MDBCarouselItem>

                        <MDBCarouselItem>

                          <MDBCarouselElement  className="cimages" src='https://i.ytimg.com/vi/rL1s7ZRk85Q/maxresdefault.jpg' alt='...' />

                          <MDBCarouselCaption>

            <h5>First slide label</h5>

            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>

          </MDBCarouselCaption>

                        </MDBCarouselItem>

                        <MDBCarouselItem>

                          <MDBCarouselElement className="cimages" src='https://i.pinimg.com/736x/1d/19/f3/1d19f37312a997646c2ae2e3b6c960bb--onam-sadhya-indian-meal.jpg' alt='...' />

                          <MDBCarouselCaption>

            <h5>First slide label</h5>

            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>

          </MDBCarouselCaption>

                        </MDBCarouselItem>

                      </MDBCarouselInner>

                    </MDBCarousel>

              </div>

              <h1 className='todayspecial'>Most Viewed Recipies</h1>

                    {/* {renderData} */}
                    {data.map((item, index) => {
                        return (
                          <>
                      <div  className="card recipecard" key={index} >


                             <img  onClick={()=>getData(item.image,item.name,item.description,item.ingredients,item.steps,item.createdAt,item._id,item.likes)}  id='recipeimage' className="card-img" src={item.image[0]} alt="Card image cap"/>
                                       

                                    <div className="card-body">
                                        
  
                                        <button className="btn  sharebutton"><img src={share} /></button>

                                       <h5 className="card-title fs-2">{item.name}</h5>

                                       <p className='fs-6'>recipe by {item.author} </p>

                                        {/* <button  onClick={()=>getlikes(item._id,item.likes)} className="btn heartbutton"><img id='hearticon' src={like}/>{item.likes}</button> */}
                                        <div className='views'><img className='viewsimage' src={views}/>{item.likes}</div>
                                        <img className='vegOrnon' src={von(item.category)} alt=""/>

                                    </div>

                    
                      <div className="card-footer">

                        <p className='posted'>posted on {NumtoDate(item.createdAt)}</p>

                      </div>
                         
                      </div>
                      
                      </>
                        )
                    } )}

               
            </div>
            <Pop trigger={Popup} setTrigger={setPop} popdata={popdata}>
                
           </Pop>
     </>   

    );
}




export default Content;